@use "variables" as *;
@import 'mixins';

.nav-wrapper {
  width: 100%;
  padding: 10px;
  display: flex;
  position: absolute;
  justify-content: space-between;
  font-family: $font-mono;

  .mobile-menu-icon {
    width: 46px;
    fill: $font-color-primary;
    opacity: 0.75;
  } 
}

nav {
  display: none;
  @include breakpoint(sm) {
    display: flex;
  }
  font-size: 1.2rem;
  align-items: center;

  ul {
    margin: 0;
    padding: 0;
    list-style: none;
  }
  
  li {
    color: $font-color-primary;
    display: inline-block;
    // font-size: 1.5rem;
  }

  a {
    color: $font-color-primary;
    padding: 6px 12px;
    text-decoration: none;
  }
}
